___CSS_LOADER_EXPORT___.push([module.id, "\n#recommendations {\n\t\tpadding: 28px 30px 0 50px;\n\t\tmargin-bottom: 20px;\n\t\tdisplay: flex;\n\t\theight: 82px;\n\t\toverflow: hidden;\n\t\tflex-wrap: wrap;\n\t\tmin-width: 0;\n}\n.c3g__categories_grid {\n\t\twidth:100%;\n\t\tdisplay: grid;\n\t\tgrid-template-columns: repeat(4, 1fr);\n}\n.card__wrapper{\n\tmargin:8px;\n}\n.card__wrapper:hover{\n\ttransform: translateY(-5px);\n\tbox-shadow:0 7px 34px rgba(50,50,93,.1),0 3px 6px rgba(0,0,0,.8);\n\ttransition:all .5s;\n}\n.card__outline{\n\theight:100%;\n\tbackground:#fff;\n\tborder-radius:5px;\n\tbox-shadow:0 2px 5px #ccc;\n\tposition:relative;\n\tdisplay:flex;\n\tjustify-content: center;\n\talign-items: center;\n\tpadding-block: 4em;\n}\n.card__tagname{\n\tfont-weight: bolder;\n\tfont-size: xx-large;\n}\n\n\t/* show 2 per line for screen sizes smaller that 1200px */\n@media only screen and (max-width: 1200px) {\n#recommendations {\n\t\t\theight: initial;\n\t\t\tmax-height: 189px;\n}\n.c3g__categories_grid {\n\t\twidth:100%;\n\t\tdisplay: grid;\n\t\tgrid-template-columns: repeat(2, 1fr);\n}\n}\n", "",{"version":3,"sources":["webpack://./src/components/Recommendations.vue"],"names":[],"mappings":";AAyLA;EACA,yBAAA;EACA,mBAAA;EACA,aAAA;EACA,YAAA;EACA,gBAAA;EACA,eAAA;EACA,YAAA;AACA;AAEA;EACA,UAAA;EACA,aAAA;EACA,qCAAA;AACA;AAEA;CACA,UAAA;AACA;AAEA;CACA,2BAAA;CACA,gEAAA;CACA,kBAAA;AACA;AACA;CACA,WAAA;CACA,eAAA;CACA,iBAAA;CACA,yBAAA;CACA,iBAAA;CACA,YAAA;CACA,uBAAA;CACA,mBAAA;CACA,kBAAA;AACA;AAEA;CACA,mBAAA;CACA,mBAAA;AACA;;CAEA,yDAAA;AACA;AACA;GACA,eAAA;GACA,iBAAA;AACA;AACA;EACA,UAAA;EACA,aAAA;EACA,qCAAA;AACA;AAEA","sourcesContent":["<!--\n  - @copyright 2019 Christoph Wurst <christoph@winzerhof-wurst.at>\n  -\n  - @author 2019 Christoph Wurst <christoph@winzerhof-wurst.at>\n  -\n  - @license GNU AGPL version 3 or any later version\n  -\n  - This program is free software: you can redistribute it and/or modify\n  - it under the terms of the GNU Affero General Public License as\